
import {
  Checkbox,
  FormControlLabel,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  MenuItem,
  TextField,
} from "@mui/material";
import { useState } from "react";
import React from "react";
import { Controller, useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import Papa from 'papaparse';


export default function InputDialog({ open, handleClose }) {
  //    useNavigate  
  const navigate = useNavigate();
  // useForm      .   
  const { register, handleSubmit, watch, control, setValue, getValues } = useForm({
    defaultValues: {
      defrost: false,
      defrostabs: false,
      defrostabsTimes:[],
      defrostabsDuration: "0",
      defrostCycle: "0",
      defrostDuration: "0",
      out: "25",
      size: "19.56",
      mass: "297",
      season: "1",
      startTime: "00:01",
      endTime: "23:59",
      currentIn: "6",
      currentOut: "25",
      schedules: [
      ]
    }

  });

  //'defrost'    
  const handleDefrostChange = (event) => {
    setValue('defrost', event.target.checked);
    // 'defrostabs'   false 
    setValue('defrostabs', false);
  };

  // 'defrostabs'    
  const handleDefrostAbsChange = (event) => {
    setValue('defrostabs', event.target.checked);
    // 'defrost'   false 
    setValue('defrost', false);
  };

  


  const defrost = watch("defrost"); //   "defrost"       .

   //    . : "9:00" "09:00" 
  const formatTime = (time) => {
    if (!time) { //    ,      .
      return "00:00"; //    "00:00" 
    }
    const [hours, minutes] = time.split(":");
    const formattedHours = hours.length === 1 ? `0${hours}` : hours;
    return `${formattedHours}:${minutes}`;
  };

  // CSV   formData   
  const transformCsvDataToFormData = (csvData) => {
    // csvData      
    return csvData.map((row, index) => ({
      id: index,
      startTime: formatTime(row[0]),
      endTime: formatTime(row[1]),
      temperature: row[2],

    }));
  };


  //     
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      //       (JSON  CSV).
      if (file.type === "application/json") {
        const reader = new FileReader();
        reader.onload = (e) => {
          const jsonContent = JSON.parse(e.target.result);

          // defrost  
          if (typeof jsonContent.defrost === 'boolean') {
            setValue('defrost', jsonContent.defrost);
          }

          // season  
          if (jsonContent.season) {
            setValue('season', jsonContent.season.toString()); // "0", "1", "2", "3"   .
          }

          // JSON     
          Object.keys(jsonContent).forEach(key => {
            if (key !== 'schedules') {
              setValue(key, jsonContent[key]);
            }
          });

          //   
          if (jsonContent.schedules && Array.isArray(jsonContent.schedules)) {
            const transformedSchedules = jsonContent.schedules.map((item, index) => ({
              ...item,
              id: index,
              startTime: formatTime(item.startTime),
              endTime: formatTime(item.endTime),
            }));
            setFormData(transformedSchedules);
            transformedSchedules.forEach((data, index) => {
              setValue(`schedules[${index}].startTime`, data.startTime);
              setValue(`schedules[${index}].endTime`, data.endTime);
              setValue(`schedules[${index}].temperature`, data.temperature);
            });
          }

          //    
          if (jsonContent.outdoorTemps && Array.isArray(jsonContent.outdoorTemps)) {
            setOutdoorTemps(jsonContent.outdoorTemps);
          }
        };
        reader.readAsText(file);
      } else if (file.type === "text/csv") {
        // CSV   ( )
        Papa.parse(file, {
          complete: function (results) {
            // CSV  formData  
            const transformedData = transformCsvDataToFormData(results.data);
            // formData  
            setFormData(transformedData);

            //      
            const outdoorTempsData = results.data.map(row => ({
              id: row[0], //   ID 
              start: formatTime(row[0]),
              end: formatTime(row[1]),
              temp: row[3]
            }));
            setOutdoorTemps(outdoorTempsData);
            // react-hook-form setValue    
            transformedData.forEach((data, index) => {
              setValue(`schedules[${index}].startTime`, data.startTime);
              setValue(`schedules[${index}].endTime`, data.endTime);
              setValue(`schedules[${index}].temperature`, data.temperature);
              setValue(`schedules[${index}].outdoorTemps`, data.outdoorTemps);
            });
          },
          header: false
        });
      }
    }
  };


  //      
  const initialSchedules = [
    { startTime: "08:00", endTime: "09:00", temperature: "6" },
    { startTime: "09:00", endTime: "12:00", temperature: "7" }
  ];

   // useState    ()   
  const [formData, setFormData] = useState(initialSchedules.map((schedule, index) => ({
    id: index,
    value: {
      start: schedule.startTime,
      end: schedule.endTime,
      tem: schedule.temperature,
    }
  })));

  //      
  const [nextId, setNextId] = useState(2);

  //     
  const handleAddInput = () => {
    setFormData([...formData, {
      id: nextId,
      startTime: "",
      endTime: "",
      temperature: ""
    }]);
    setNextId(nextId + 1);
  };


  //     
  const handleChange = (id, field, value) => {
    const updatedFormData = formData.map((item) => {
      if (item.id === id) {
        return { ...item, [field]: value }; //  
      }
      return item;
    });
    setFormData(updatedFormData);
  };



  //    
  const handleRemoveInput = (id) => {
    console.log("Removing ID:", id);
    setFormData(formData.filter((data) => data.id !== id));
  };

  //    
  const seasonOption = [
    {
      value: "0",
      label: "",
    },
    {
      value: "1",
      label: "",
    },
    {
      value: "2",
      label: "",
    },
    {
      value: "3",
      label: "",
    },
  ];
  //     
  const [outdoorTemps, setOutdoorTemps] = useState([
    { id: 0, start: '08:00', end: '09:00', temp: '25' },
    { id: 1, start: '09:00', end: '12:00', temp: '25' },
  ]);

  //    ID  
  const [nextOutdoorTempId, setNextOutdoorTempId] = useState(2);

  //     
  const handleAddOutdoorTemp = () => {
    const newOutdoorTemp = { id: nextOutdoorTempId, start: '', end: '', temp: '' };
    setOutdoorTemps([...outdoorTemps, newOutdoorTemp]);
    setNextOutdoorTempId(nextOutdoorTempId + 1);
  };

  //     
  const handleChangeOutdoorTemp = (id, field, value) => {
    const updatedOutdoorTemps = outdoorTemps.map((temp) =>
      temp.id === id ? { ...temp, [field]: value } : temp
    );
    setOutdoorTemps(updatedOutdoorTemps);
  };

  //     
  const handleRemoveOutdoorTemp = (id) => {
    setOutdoorTemps(outdoorTemps.filter((temp) => temp.id !== id));
  };

  //  doorTime   
  const [doorTime, setDoorTime] = useState("");

  //    useState 
  const [doorOpenTimes, setDoorOpenTimes] = useState([]);

  //     
  const handleAddDoorOpenEvent = () => {
    console.log("Added Door Open Time:", doorTime);
    setDoorOpenTimes(prevTimes => [...prevTimes, doorTime]);
  }

  const handleRemoveDoorOpenEvent = (index) => {
    const newDoorOpenTimes = [...doorOpenTimes]
    newDoorOpenTimes.splice(index, 1);
    setDoorOpenTimes(newDoorOpenTimes);
  }




  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{ sx: { borderRadius: "15px", width: "calc(70vw)" } }}
      >
        <form
          onSubmit={handleSubmit((data) => {
            //   
            const submissionData = {
              ...data,
              doorOpenTimes,
              schedules: formData, //     formData   
              outdoorTemps, //  

            };
            console.log("Submitting Data:", submissionData); //    
            navigate("/result", {
              state: submissionData, //  
            });
          }
          )}
        >
          <DialogTitle>User Input Info</DialogTitle>
          <DialogContent>
            <FormControlLabel
              control={
                <Checkbox
                  {...register("defrost")}
                  checked={!!watch("defrost")} // "!!"    boolean   .
                  onChange={handleDefrostChange}
                />
              }
              label="  "
            />
            <FormControlLabel
              control={
                <Checkbox
                  {...register("defrostabs")}
                  checked={!!watch("defrostabs")} // "!!"    boolean   .
                  onChange={handleDefrostAbsChange}
                />
              }
              label="    "
            />

            <br></br>
            {watch("defrost")&&(
              <>
              <TextField
                margin="dense"
                id="defrostCycle"
                label=""
                fullWidth
                variant="standard"
                {...register("defrostCycle")}
              />
              <TextField
                margin="dense"
                id="defrostDuration"
                label=""
                fullWidth
                variant="standard"
                {...register("defrostDuration")}
              />
              </>
            )}
            
            {/* <TextField
              type="time"
              margin="dense"
              id="defrostabsTime"
              label="  "
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("defrostabsTime")}
            /> */}
            {watch("defrostabs")&&(
              <>
                {formData.map((data, index) => (
                  <div key={data.id}>
                    <TextField
                      label={`   ${index + 1}`}
                      type="time"
                      {...register(`defrostabsTimes[${index}].absTime`)}
                      defaultValue={getValues(`defrostabsTimes[${index}].absTime`)}
                      onChange={(e) => {
                        setValue(`defrostabsTimes[${index}].absTime`, e.target.value);
                        handleChange(data.id, 'absTime', e.target.value);
                      }}
                    />
                    <Button onClick={() => handleRemoveInput(data.id)}></Button>
                  </div>
                ))}
                <Button variant="contained" onClick={handleAddInput}>+  </Button>
                <TextField
                  margin="dense"
                  id="defrostabsDuration"
                  label=""
                  fullWidth
                  variant="standard"
                  {...register("defrostabsDuration")}
                />

              </>
            )}
            
            <TextField
              margin="dense"
              id="out"
              label=" "
              fullWidth
              variant="standard"
              {...register("out")}
            />
            <TextField
              margin="dense"
              id="size"
              label=" "
              fullWidth
              variant="standard"
              {...register("size")}
            />
            <TextField
              margin="dense"
              id="mass"
              label="   "
              fullWidth
              variant="standard"
              {...register("mass")}
            />
            <TextField
              select
              label=""
              {...register("season")}
              value={watch("season", "1")} //  "1" 
              fullWidth
              variant="standard"
            >
              {seasonOption.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <FormControlLabel
            control={
            <Checkbox{...register("showAnnotations")}
            />
          }label="  "/>
            <TextField
              type="time"
              margin="dense"
              id="startTime"
              label="  "
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("startTime")}
            />
            <TextField
              type="time"
              margin="dense"
              id="endTime"
              label="  "
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("endTime")}
            />
            {/* <TextField
              type="time"
              margin="dense"
              id="doorTime"
              label="  "
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("doorTime")}
            /> */}
                <br></br>
            <TextField
              type="time"
              value={doorTime}
              onChange={(e) => setDoorTime(e.target.value)}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <Button onClick={handleAddDoorOpenEvent}>   </Button>
            <ul>
              {doorOpenTimes.map((time, index) => (
                <li key={index}>
                  {time}
                  <Button onClick={() => handleRemoveDoorOpenEvent(index)}></Button>
                </li>
              ))}
            </ul>
            <TextField
              margin="dense"
              id="currentIn"
              label="   ()"
              fullWidth
              variant="standard"
              {...register("currentIn")}
            />
            <TextField
              margin="dense"
              id="currentOut"
              label="   ()"
              fullWidth
              variant="standard"
              {...register("currentOut")}
            />
              
            <input
              type="file"
              accept=".csv, application/json"
              onChange={handleFileUpload}
            />

            {formData.map((data, index) => (
              <div key={data.id}>
                <TextField
                  label={`  ${index + 1}`}
                  type="time"
                  {...register(`schedules[${index}].startTime`)}
                  defaultValue={getValues(`schedules[${index}].startTime`)}
                  onChange={(e) => {
                    setValue(`schedules[${index}].startTime`, e.target.value);
                    handleChange(data.id, 'startTime', e.target.value);
                  }}
                />
                <TextField
                  label={`  ${index + 1}`}
                  type="time"
                  {...register(`schedules[${index}].endTime`)}
                  defaultValue={getValues(`schedules[${index}].endTime`)}
                  onChange={(e) => {
                    setValue(`schedules[${index}].endTime`, e.target.value);
                    handleChange(data.id, 'endTime', e.target.value);
                  }}
                />
                <TextField
                  label={`  ${index + 1}`}
                  type="standard"
                  {...register(`schedules[${index}].temperature`)}
                  defaultValue={getValues(`schedules[${index}].temperature`)}
                  onChange={(e) => {
                    // react-hook-form  
                    setValue(`schedules[${index}].temperature`, e.target.value);
                    //    
                    handleChange(index, 'temperature', e.target.value); //  data.id  index 
                  }}
                />


                <Button onClick={() => handleRemoveInput(data.id)}></Button>
              </div>
            ))}
            <Button variant="contained" onClick={handleAddInput}>+  </Button>

            {/*     */}
            <br></br>
               ( )
            {outdoorTemps.map((temp, index) => (
              <Box key={temp.id} sx={{ display: 'flex', gap: 2, marginBottom: 2 }}>
                <TextField
                  label={`  ${index + 1}`}
                  type="time"
                  value={temp.start}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'start', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <TextField
                  label={`  ${index + 1}`}
                  type="time"
                  value={temp.end}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'end', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <TextField
                  label={`  ${index + 1}`}
                  type="standard"
                  value={temp.temp}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'temp', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <Button variant="outlined" color="error" onClick={() => handleRemoveOutdoorTemp(temp.id)}>
                  
                </Button>
              </Box>
            ))}
            <Button variant="contained" onClick={handleAddOutdoorTemp}>+  </Button>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}></Button>
            {/* <Link to={"/result"}> */}
            <Button type="submit"></Button>
            {/* </Link> */}
          </DialogActions>
        </form>
      </Dialog>
    </>
  );
}