import { Typography } from "@mui/material";
import ApexChart from "react-apexcharts";


export default function Graph({ title, xData, yData, yData2 = [], yAxisTitle = "", startHour = 0, endHour = 24, showAnnotations = true  }) {
  //      
  const getAnnotationColor = (hour) => {
    //    
    if (hour >= 0 && hour < 8 || hour >= 22 && hour <= 24) return '#008FFB'; // 
    if (hour >= 8 && hour < 9 || hour >= 12 && hour < 16 || hour >= 19 && hour < 22) return '#FF0000'; // 
    if (hour >= 9 && hour < 12 || hour >= 16 && hour < 19) return '#FFFF00'; // 
    return null;
  };
  
  //      
  const getAnnotationLabel = (hour) => {
    //    
    if (hour >= 0 && hour < 8 || hour >= 22 && hour <= 24) return '';
    if (hour >= 8 && hour < 9 || hour >= 12 && hour < 16 || hour >= 19 && hour < 22) return '';
    if (hour >= 9 && hour < 12 || hour >= 16 && hour < 19) return '';
    return null;
  };

  //     
  const annotations = [];
  const pointAnnotations = []; // point   
  const yMin = Math.min(...yData) - 0.5; // yData  5  
  const yMax = Math.max(...yData) + 0.5; // yData  5  


  // xData     
  xData.forEach((time, index) => {
    const hour = parseInt(time.split(":")[0]);
    const prevHour = parseInt(xData[index - 1]?.split(":")[0] || -1);
    if (hour >= startHour && hour < endHour) {
        const fillColor = getAnnotationColor(hour);
        const prevFillColor = getAnnotationColor(prevHour);
        const labelText = getAnnotationLabel(hour);
        if (fillColor && labelText) {
          if(showAnnotations){
            annotations.push({
              x: time,
              x2: xData[index + 1] || (hour + 1) + ":00",
              fillColor: fillColor,
              opacity: 0.2
          });
          }
            if (fillColor !== prevFillColor || index === 0) { //          
                pointAnnotations.push({ // point  
                    x: time,
                    label: {
                        borderColor: 'transparent',
                        offsetY: -10, //   
                        style: {
                            color: '#333',
                            background: 'transparent',
                        },
                        text: labelText
                    }
                });
            }
        }
    }
  }
  
  );

  //   JSX 
  return (
    <>
      <Typography ml={1} mt={1}>
        {title}
      </Typography>
      <ApexChart
        type="area"
        series={[
          {
            name: "",
            data: yData,
          },
          {
            name: "",
            data: yData2,
          }
        ]}
        options={{
          chart: {
            height: 200,
            type: "area",
            toolbar: {
              show: true,
              tools: {
                zoom: true,
                zoomin: true,
                zoomout: true,
                pan: true,
                reset: true
              }
            },
            zoom: {
              enabled: true,
              type: 'x', // 'x', 'y'  'xy'      
              autoScaleYaxis: true
            },

          },
          colors: ['#8B00FF', '#FF4560'],
          dataLabels: {
            enabled: false,
          },
          stroke: {
            curve: "smooth",
          },
          xaxis: {
            type: "string",
            categories: xData,
            labels: {
              show: true,
            },
            tickAmount:10,
            title: {
              text: "(hh:mm)",
              rotate: -90,
            }
          },
          yaxis: {
            min: yMin,
            max: yMax,
            labels: {
              formatter: (value) => {
                return value? value.toFixed(2):'0.00'; //  2 
              },
            },
            title: {
              text: yAxisTitle,
              rotate: -90,
            }
          },
          legend: {
            show: false,
          },
          fill: {
            type: "gradient",
            gradient: {
              shadeIntensity: 1,
              opacityFrom: 0.7,
              opacityTo: 0.9,
              stops: [0, 90, 100],
            },
          },
          annotations: {
            xaxis: annotations,
            points: pointAnnotations // point annotations 
          }
        }}
      />
    </>
  );
}